import React from 'react';
import {Link} from 'react-router-dom';
import './customize.scss';


function NavBar(props) {


    const onClick = e => {

        const $target = document.getElementById(e.target.dataset.target);

        e.target.classList.toggle('is-active')
        $target.classList.toggle('is-active');
    };


    return(
        <nav className="navbar" role="navigation" aria-label="main navigation">


            {/* logo */}
            <div className="navbar-brand">
                <Link to="/" className="navbar-item">&nbsp;&nbsp;
                    <img src=""/>

                    <h1 className="title is-5 is-family-primary">&nbsp; Servicell SJK</h1>
                </Link>

                <a role="button" className="navbar-burger" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample" onClick={onClick}>
                    <span aria-hidden="true" data-target="navbarBasicExample"></span>
                    <span aria-hidden="true" data-target="navbarBasicExample"></span>
                    <span aria-hidden="true" data-target="navbarBasicExample"></span>
                </a>
            </div>

            {/* items */}
            <div id="navbarBasicExample" className="navbar-menu">
                <div className="navbar-end mr-3">
                    <Link to="/" className="navbar-item"><span className="icon"><i className="fas fa-home"></i></span>&nbsp;&nbsp;Inicio</Link>
                    <Link to="/catalogo" className="navbar-item"><span className="icon"><i className="fas fa-mobile"></i></span>&nbsp;&nbsp;Catalogo</Link>
                    <Link to="/nosotros" className="navbar-item"><span className="icon"><i className="fas fa-users"></i></span>&nbsp;&nbsp;Nosotros</Link>
                    <Link to="/empresa" className="navbar-item"><span className="icon"><i className="fas fa-building"></i></span>&nbsp;&nbsp;Empresa</Link>
                </div>
            </div>


        </nav>
    );

}

export default NavBar;
