import React from 'react';

import phoneImg from '../../video/phone.png';
import video from '../../video/background.mp4';
import {Link} from 'react-router-dom';
import './home.scss';


function Home(props) {


    return(
        <div>
            <section class="hero is-fullheight-with-navbar video" >
            <div class="hero-video">
                <div className="gradient"></div>
                <video autoPlay muted loop style={{opacity: 0.8}}><source src={video} type="video/mp4"/></video>
            </div>
            <div class="hero-body">

                <div className="container">
                    <div className="columns is-desktop is-vcentered">

                    <div className="column cel">
                        <figure class="image" style={{zIndex: 3, opacity: 0.8}}>
                            <img src={phoneImg} />
                        </figure>
                    </div>

                    <div class="column" style={{zIndex:3}}>
                        <h1 className="title is-1 has-text-white"> Servicell SJK </h1>
                        <h1 className="subtitle is-4 has-text-white"> Tecnología de impresión 3D para forros de celulares </h1>

                        <p className="subtitle is-5 has-text-white has-text-justified">
                         Sabemos lo importante que es tu celular para ti, por eso creamos fundas 100% personalizadas para tu telefono móvil sin importar su marca o antigüedad, con nuestras fundas tu celular tendrá una vida mas larga y feliz.

                        </p>

                        <Link to="/catalogo" className="button is-info is-light">Iniciar</Link>
                    </div>


                    </div>


                    </div>
                </div>

            </section>

        </div>
    );

}

export default Home;
