import React, { useState } from 'react';

function PhoneCard(props) {

    const [isLiked, setLike] = useState(false);

    const onClick = e => {
        e.preventDefault();
        setLike(!isLiked);
    }

    return(
        <div className="card">


            <div className="card-image">

                <div className="container" style={{position: 'absolute', right: '0', zIndex: '10', margin: '5px 10px 0 0'}}>
                    <a className="subtitle has-text-white" onClick={onClick}><span className="icon" style={{color: isLiked ? 'red' : 'white'}}><i className={`${isLiked ? 'fas' : 'far'} fa-heart`}></i></span></a>
                </div>

                <div className="card-image">
                    <figure className="image">
                        <img src="https://cdn.thingiverse.com/renders/8e/28/27/93/fd/untitled.15_preview_featured.jpg" alt="Placeholder image"/>
                    </figure>
                </div>


                <div className="card-content">
                    <h1 className="title is-6 has-text-centered">{props.name}</h1>

                    <div className="content">
                        {props.children}

                        <br/>
                        <time dateTime="2016-1-1">11:09 PM - 1 Jan 2016</time>
                    </div>
                </div>


            </div>
        </div>
    );

}

export default PhoneCard;
